.flat {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex; }

.flat__description-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  -webkit-column-gap: 2%;
     -moz-column-gap: 2%;
          column-gap: 2%; }

.flat__description,
.flat__description-term {
  min-width: 120px; }
  .flat__description:nth-of-type(-n + 2),
  .flat__description-term:nth-of-type(-n + 2) {
    grid-column: 1 / 2; }
  .flat__description:nth-of-type(n + 3):nth-of-type(-n + 4),
  .flat__description-term:nth-of-type(n + 3):nth-of-type(-n + 4) {
    grid-column: 2 / 3; }
  .flat__description:nth-of-type(n + 5),
  .flat__description-term:nth-of-type(n + 5) {
    grid-column: 3 / -1; }

.flat__description {
  margin: 0;
  margin-bottom: 12px;
  font-weight: 500;
  color: #383736; }
  .flat__description:nth-of-type(2n + 1) {
    grid-row: 2 / 3; }
  .flat__description:nth-of-type(2n) {
    grid-row: 4 / 5; }

.flat__description-term {
  color: #989691; }
  .flat__description-term:nth-of-type(2n + 1) {
    grid-row: 1 / 2; }

.flat__conditions-view,
.flat__conditions-bank {
  margin: 0;
  padding-top: 6px;
  padding-right: 16px;
  padding-bottom: 6px;
  padding-left: 36px;
  font-weight: 500;
  font-size: 12px;
  border-radius: 22px; }

.container-edge {
  margin: 0 auto;
  padding-left: 16.8%;
  padding-right: 16.3%; }

@media (max-width: 919px) {
  .container-edge {
    padding-left: 45px;
    padding-right: 45px; } }

@media (max-width: 767px) {
  .container-edge {
    padding-left: 15px;
    padding-right: 15px; } }

.container-background {
  position: relative;
  background-color: #e5e5e5; }

.btn {
  padding: 12px 50px;
  font-weight: 500;
  color: #383736;
  background-color: #fac62d;
  border: none;
  border-radius: 6px;
